ked\"\n         aria-label=\"加数\"\n         :accessible=\"true\">\n      <text class=\"stepper-icon\" :style=\"{ color: isOver ? '#cccccc': '#666666' }\">+</text>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n  .wxc-stepper {\n    flex-direction: row;\n  }\n\n  .stepper-plus, .stepper-minus {\n    width: 56px;\n    height: 56px;\n    background-color: #ededed;\n    align-items: center;\n    justify-content: center;\n    border-radius: 6px;\n  }\n\n  .stepper-input {\n    border-width: 0;\n    text-align: center;\n    color: #3d3d3d;\n    font-size: 30px;\n    line-height: 56px;\n    width: 86px;\n  }\n\n  .stepper-icon {\n    font-size: 36px;\n    color: #666666;\n    margin-top: -4px;\n  }\n\n</style>\n\n<script>\n  export default {\n    props: {\n      min: {\n        type: [String, Number],\n        default: 1\n      },\n      max: {\n        type: [String, Number],\n        default: 100\n      },\n      step: {\n        type: [String, Number],\n        default: 1\n      },\n      disabled: {\n        type: Boolean,\n        default: false\n      },\n      defaultValue: {\n        type: [String, Number],\n        default: 1\n      },\n      readOnly: {\n        type: Boolean,\n        default: false\n      }\n    },\n    computed: {\n      disableStyle () {\n        if (this.disabled) {\n          return {\n            color: '#cccccc'\n          }\n        }\n      },\n      valueString () {\n        return this.value.toString();\n      }\n    },\n    data: () => ({\n      value: 1,\n      isLess: false,\n      isOver: false\n    }),\n    watch: {\n      defaultValue (newNum) {\n        this.value = newNum;\n      }\n    },\n    created () {\n      this.value = parseInt(this.defaultValue, 10);\n      if (this.disabled) {\n        this.isLess = true;\n        this.isOver = true;\n      }\n    },\n    methods: {\n      minusClicked () {\n        if (this.disabled) {\n          return;\n        }\n        const isMinOver = this.value <= this.min;\n        const nowNum = this.value - parseInt(this.step, 10);\n        if (isMinOver) {\n          this.$emit('wxcStepperValueIsMinOver', { value: this.value });\n        } else {\n          this.value = nowNum;\n          this.resetDisabledStyle();\n        }\n        // 由于此处已经减step\n        if (nowNum <= this.min) {\n          this.value = parseInt(this.min, 10);\n          this.isLess = true;\n        }\n        this.$emit('wxcStepperValueChanged', { value: this.value });\n      },\n      plusClicked () {\n        if (this.disabled) {\n          return;\n        }\n        const isMaxOver = this.value >= this.max;\n        const nowNum = this.value + parseInt(this.step, 10);\n        if (isMaxOver) {\n          this.$emit('wxcStepperValueIsMaxOver', { value: this.value });\n        } else {\n          this.value = nowNum;\n          this.resetDisabledStyle();\n        }\n        // 由于此处已经加step\n        if (nowNum >= this.max) {\n          this.value = parseInt(this.max, 10);\n          this.isOver = true;\n        }\n        this.$emit('wxcStepperValueChanged', { value: this.value });\n      },\n      onInput (e) {\n        this.correctInputValue(e.value);\n      },\n      onBlur (e) {\n        this.correctInputValue(e.value);\n      },\n      correctInputValue (v) {\n        if (/^[1-9]\\d{0,}$/.test(v) && parseInt(v, 10) >= this.min && parseInt(v, 10) <= this.max) {\n          this.value = parseInt(v, 10);\n        }\n        this.$emit('wxcStepperValueChanged', { value: this.value });\n      },\n\n      resetDisabledStyle () {\n        this.isLess = false;\n        this.isOver = false;\n      }\n    }\n  };\n</script>\n"],"sourceRoot":""}]);

